*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1 {
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-size: calc(10px + 8vw);
    font-style: normal;
    font-weight: 700;    
    letter-spacing: 0.02em; 
    position:relative;
}

.profile-holder {
    margin: 3rem;
    position: relative;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: visible;
}

.profile-pic, .profile-heading{
    object-fit: cover;
    position: absolute;
    bottom: auto;
}

.profile-pic {
    filter: brightness(1.15);
    bottom: 23.88vh;
    height: auto;
    max-height: 50vh;
    margin-left: auto;
    margin-right: auto;
}

.profile-background {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 80%;
    height: 40%;
    background-color: #f8c6c6;
    z-index: -1;

    display: flex;
    justify-content: center;
    align-items: center;
}

/* center both headings like the James layout */
.profile-heading {
    left: 50%;
    top: 70%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center;
}

/* white text sits on top, centered */
/* #first {
    color: #fff;
    z-index: 2;
} */
#first {
    color: #fff;
    z-index: 2;
    clip-path: inset(0 0 50% 0); /* show top 50% only */
}

#second {
    color: #f8c6c6;
    z-index: 1;                 /* place below #first so its banner can sit behind both */
    clip-path: inset(50% 0 0 0); /* show bottom 50% only */
}

/* Mobile layout */
@media (max-width: 768px) {
    .profile-heading {
        top: 50%;
    }

    #first {
        width: max-content;        /* size to text only */
        padding: 0 .35em;          /* banner side spacing */
    }
    #first::before, #second::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 1.2em;             /* banner height ~ line-height */
        background: rgba(0, 0, 0, 0.12); /* light, transparent grey */
        border-radius: 0.15em;
        z-index: -1;               /* sit behind the white text */
    }
}